import React from 'react';
import courseImagesMap from '../pics/courseImages';

const CourseItem = ({ course, onClick }) => {
  // Get the correct image from the map or fallback to the original path
  const courseImage = courseImagesMap[course.CourseImage] || course.CourseImage;

  return (
    <div className="course-item" onClick={() => onClick(course)}>
      <div className="image-container">
        <img 
          src={courseImage} 
          alt={course.courseName} 
          className="course-image" 
        />
        <span className="course-type">{course.Type}</span>
      </div>
    </div>
  );
};

export default CourseItem;
