import React, { useState } from 'react';
import './reading.css'

const ReadingUseOfEnglish = ({ nextPart }) => {
    const passage = `
      In recent years, the rise of social media platforms has significantly transformed the way people communicate. 
      Platforms like Facebook, Twitter, Instagram, and LinkedIn have provided users with new ways to interact, 
      share information, and stay connected with others around the globe. These changes have had profound effects 
      on personal relationships, professional networking, and even political activism.
  
      One of the most significant impacts of social media is the way it has changed interpersonal communication. 
      Traditionally, communication was limited to face-to-face interactions, phone calls, and written correspondence. 
      However, social media has introduced new forms of communication, such as instant messaging, video calls, and public posts. 
      These platforms allow people to maintain relationships over long distances and stay in touch with a broader network of acquaintances.
  
      Social media has also revolutionized the professional world. Networking sites like LinkedIn have become essential 
      tools for job seekers and employers alike. Professionals can connect with others in their industry, share their work, 
      and even find new job opportunities. This has made the job market more competitive and dynamic, with individuals 
      needing to constantly update their profiles and skills to stay relevant.
  
      However, the rise of social media has not been without its drawbacks. Critics argue that it has led to a decline in 
      face-to-face communication and has contributed to the spread of misinformation. Additionally, the constant use of 
      social media can lead to issues such as anxiety, depression, and addiction. The curated nature of social media content, 
      where users often only share the highlights of their lives, can create unrealistic expectations and feelings of inadequacy among viewers.
  
      Moreover, social media has played a significant role in political activism and public discourse. Movements like #MeToo 
      and Black Lives Matter have gained momentum through social media, allowing activists to reach a global audience quickly 
      and efficiently. However, this has also led to the spread of extremist views and the manipulation of public opinion through 
      targeted misinformation campaigns.
  
      In conclusion, social media has dramatically altered the landscape of communication, with both positive and negative consequences. 
      While it has made it easier to stay connected and share information, it has also introduced new challenges, such as the decline 
      in personal interaction and the rise of misinformation. As social media continues to evolve, it will be important to find a balance 
      that maximizes its benefits while minimizing its negative impacts.
    `;
  
    const paragraphs = passage.trim().split('\n\n');
  
    const [userAnswers, setUserAnswers] = useState({});
    const [showResults, setShowResults] = useState(false);

  const questions = [
    {
      id: 1,
      type: 'multiple-choice',
      question: 'What is one of the most significant impacts of social media on communication?',
      options: [
        'It has reduced the need for written correspondence.',
        'It has limited the ways people can stay in touch.',
        'It has introduced new forms of communication.',
        'It has made face-to-face communication more popular.'
      ],
      correctAnswer: 'It has introduced new forms of communication.'
    },
    {
      id: 2,
      type: 'multiple-choice',
      question: 'According to the passage, how has social media affected the professional world?',
      options: [
        'It has made job hunting more difficult.',
        'It has become a less relevant tool for professionals.',
        'It has led to fewer networking opportunities.',
        'It has become essential for professional networking.'
      ],
      correctAnswer: 'It has become essential for professional networking.'
    },
    {
      id: 3,
      type: 'multiple-choice',
      question: 'What is one of the criticisms mentioned about social media?',
      options: [
        'It has improved face-to-face communication.',
        'It has contributed to the spread of misinformation.',
        'It has made it easier to avoid anxiety and depression.',
        'It has stopped the growth of political activism.'
      ],
      correctAnswer: 'It has contributed to the spread of misinformation.'
    },
    {
      id: 4,
      type: 'multiple-choice',
      question: 'What does the passage suggest about the future of social media?',
      options: [
        'It will likely eliminate face-to-face communication entirely.',
        'It will continue to evolve and present new challenges.',
        'It will stop being a tool for professional networking.',
        'It will reduce the amount of misinformation online.'
      ],
      correctAnswer: 'It will continue to evolve and present new challenges.'
    },
    {
      id: 5,
      type: 'true-false',
      question: 'Social media has completely replaced traditional forms of communication.',
      correctAnswer: 'False'
    },
    {
      id: 6,
      type: 'true-false',
      question: 'LinkedIn is specifically mentioned as a platform that has revolutionized job seeking and professional networking.',
      correctAnswer: 'True'
    },
    {
      id: 7,
      type: 'true-false',
      question: 'The passage states that social media has only positive effects on communication.',
      correctAnswer: 'False'
    },
    {
      id: 8,
      type: 'true-false',
      question: 'Social media has played a significant role in political activism according to the passage.',
      correctAnswer: 'True'
    },
    {
      id: 9,
      type: 'multiple-choice',
      question: 'How has social media impacted political movements like #MeToo and Black Lives Matter?',
      options: [
        'It has slowed down their momentum.',
        'It has allowed them to reach a global audience quickly.',
        'It has prevented their messages from being shared widely.',
        'It has made them irrelevant.'
      ],
      correctAnswer: 'It has allowed them to reach a global audience quickly.'
    },
    {
      id: 10,
      type: 'true-false',
      question: 'Social media platforms are exclusively beneficial for professional networking.',
      correctAnswer: 'False'
    },
  ];

  const handleAnswer = (questionId, isCorrect) => {
    setUserAnswers({
      ...userAnswers,
      [questionId]: isCorrect,
    });
  };

  const renderQuestion = (question) => {
    if (question.type === 'multiple-choice') {
      return (
        <div className="question" key={question.id}>
          <p>{question.question}</p>
          {question.options.map((option, index) => (
            <button
              key={index}
              onClick={() => handleAnswer(question.id, option === question.correctAnswer)}
              disabled={userAnswers[question.id] !== undefined} // Disable button if already answered
            >
              {option}
            </button>
          ))}
        </div>
      );
    }

    if (question.type === 'true-false') {
      return (
        <div className="question" key={question.id}>
          <p>{question.question}</p>
          <button
            onClick={() => handleAnswer(question.id, 'True' === question.correctAnswer)}
            disabled={userAnswers[question.id] !== undefined}
          >
            True
          </button>
          <button
            onClick={() => handleAnswer(question.id, 'False' === question.correctAnswer)}
            disabled={userAnswers[question.id] !== undefined}
          >
            False
          </button>
        </div>
      );
    }

    return null;
  };

  const handleSubmit = () => {
    setShowResults(true);
  };

  const renderResults = () => {
    const correctAnswersCount = Object.values(userAnswers).filter(answer => answer).length;
    return (
      <div className="results">
        <p>You got {correctAnswersCount} out of {questions.length} correct.</p>
        <button onClick={nextPart}>Next Part</button>
      </div>
    );
  };

  return (
    <div className="reading-use-of-english-grid">
      <div className="passage">
        {paragraphs.map((paragraph, index) => (
          <p key={index}>{paragraph}</p>
        ))}
      </div>
      <div className="questions">
        {questions.map(question => renderQuestion(question))}
        <button onClick={handleSubmit}>Submit</button>
        {showResults && renderResults()}
      </div>
    </div>
  );
};

export default ReadingUseOfEnglish;