import React, { useState, useEffect } from 'react';
import { collection, getDocs, doc, updateDoc } from 'firebase/firestore';
import { db } from '../../firebase'; // Adjust the import path as needed
import '../../styles/AdminPage.css'

const UserManagement = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [editingUser, setEditingUser] = useState(null);
  const [formData, setFormData] = useState({});
  const [visibleSections, setVisibleSections] = useState({}); // Track visibility of finishedLessons and finishedWorkbooks

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, 'users'));
        const usersList = [];
        querySnapshot.forEach((doc) => {
          usersList.push({ id: doc.id, ...doc.data() });
        });
        setUsers(usersList);
      } catch (err) {
        console.error('Error fetching users:', err);
        setError('Failed to fetch users.');
      } finally {
        setLoading(false);
      }
    };

    fetchUsers();
  }, []);

  const handleEditClick = (user) => {
    setEditingUser(user.id);
    setFormData(user);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSaveClick = async () => {
    if (editingUser) {
      try {
        await updateDoc(doc(db, 'users', editingUser), formData);
        setUsers((prevUsers) =>
          prevUsers.map((user) => (user.id === editingUser ? formData : user))
        );
        setEditingUser(null);
      } catch (err) {
        console.error('Error updating user:', err);
        setError('Failed to update user.');
      }
    }
  };

  const toggleSectionVisibility = (userId, section) => {
    setVisibleSections((prevState) => ({
      ...prevState,
      [userId]: {
        ...prevState[userId],
        [section]: !prevState[userId]?.[section],
      },
    }));
  };

  const renderArray = (arr) => (
    <ul>
      {arr.map((item, index) => (
        <li key={index}>{JSON.stringify(item)}</li>
      ))}
    </ul>
  );

  if (loading) return <p>Loading users...</p>;
  if (error) return <p style={{ color: 'red' }}>{error}</p>;

  return (
    <div>
      <h2>User Management</h2>
      <div className='userMg'>
      {users.map((user) => (
        <div>
          {editingUser === user.id ? (
            <div>
              {Object.keys(formData).map((key) => (
                <div key={key} style={{ marginBottom: '10px' }}>
                  <label>{key}:</label>
                  {typeof formData[key] === 'object' && formData[key] !== null ? (
                    <div style={{ marginLeft: '10px' }}>
                      {renderArray(formData[key])}
                    </div>
                  ) : (
                    <input
                      type="text"
                      name={key}
                      value={formData[key] || ''}
                      onChange={handleInputChange}
                      placeholder={`Enter ${key}`}
                      style={{ marginLeft: '10px' }}
                    />
                  )}
                </div>
              ))}
              <button onClick={handleSaveClick}>Save</button>
              <button onClick={() => setEditingUser(null)}>Cancel</button>
            </div>
          ) : (
            <div>
              {Object.entries(user).map(([key, value]) => (
                
                <p key={key}>
                  <strong>{key}:</strong>{' '}
                  {Array.isArray(value) ? (
                    <>
                      <button
                        onClick={() => toggleSectionVisibility(user.id, key)}
                      >
                        {key} ({value.length})
                      </button>
                      {visibleSections[user.id]?.[key] && (
                        <div style={{ marginLeft: '20px' }}>
                          {renderArray(value)}
                        </div>
                      )}
                    </>
                  ) : typeof value === 'object' && value !== null ? (
                    JSON.stringify(value)
                  ) : (
                    value
                  )}
                </p>
              ))}
              <button onClick={() => handleEditClick(user)}>Edit</button>
            </div>
          )}
        </div>
      ))}
    </div></div>
  );
};

export default UserManagement;
