import React, { useState } from 'react';
import ReadingUseOfEnglish from './ReadingUseOfEnglish';
import Writing from './Writing';
import Listening from './Listening';
import Speaking from './Speaking';

const EngTest = () => {
  const [currentPart, setCurrentPart] = useState('reading');

  const renderPart = () => {
    switch (currentPart) {
      case 'reading':
        return <ReadingUseOfEnglish nextPart={() => setCurrentPart('writing')} />;
      case 'writing':
        return <Writing nextPart={() => setCurrentPart('listening')} />;
      case 'listening':
        return <Listening nextPart={() => setCurrentPart('speaking')} />;
      case 'speaking':
        return <Speaking />;
      default:
        return <ReadingUseOfEnglish nextPart={() => setCurrentPart('writing')} />;
    }
  };

  return (
    <div className="app-container">
      {renderPart()}
    </div>
  );
};

export default EngTest;
