import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import DOMPurify from 'dompurify';
import '../../styles/CurrentLesson.css'; 
import '../../styles/LessonPage.css'; 
import { handleInteraction, PART_KEYS } from './utils';
import { useLearning } from '../../contexts/LearningContext';
import { useAuth } from '../../contexts/AuthContext'; 
import { getFirestore, } from 'firebase/firestore';
import leftIcon from '../../assets/left.svg';
import rightIcon from '../../assets/right.svg';

const LessonPage = () => {
  const { lessonID } = useParams();
  const { selectedCourse, finishLesson } = useLearning(); // Import finishLesson
  const [lesson, setLesson] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [currentPartIndex, setCurrentPartIndex] = useState(0);
  const [currentPartName, setCurrentPartName] = useState('');
  const [startTime, setStartTime] = useState(null);
  const [correctAnswers, setCorrectAnswers] = useState(0);
  const [totalQuestions, setTotalQuestions] = useState(0);
  const [userInteractions, setUserInteractions] = useState({});
  const navigate = useNavigate();

  useEffect(() => {
    if (selectedCourse) {
      const lessonData = selectedCourse.lessons.find(lesson => lesson.id === lessonID);
      if (lessonData) {
        setLesson(lessonData);
        calculateTotalQuestions(lessonData);
        updateCurrentPartName(lessonData, 0);
        setStartTime(Date.now());
        setLoading(false);
        applyCustomCSS(lessonData.css);
      } else {
        setError('Lesson not found');
        setLoading(false);
      }
    } else {
      setError('Selected course data not available');
      setLoading(false);
    }
  }, [selectedCourse, lessonID]);

  useEffect(() => {
    if (lesson) {
      const cleanup = handleInteraction(); 
      updateCurrentPartName(lesson, currentPartIndex);
      return cleanup;
    }
  }, [lesson, currentPartIndex]);

  const applyCustomCSS = (css) => {
    const sanitizedCSS = DOMPurify.sanitize(css || '');
    if (sanitizedCSS) {
      const styleElement = document.createElement('style');
      styleElement.textContent = sanitizedCSS;
      document.head.appendChild(styleElement);

      return () => {
        document.head.removeChild(styleElement);
      };
    }
  };

  const calculateTotalQuestions = (lesson) => {
    const total = PART_KEYS.filter(key => key.includes('input') && lesson[key]).length;
    setTotalQuestions(total);
  };

  const updateCurrentPartName = (lesson, index) => {
    if (index < 5) {
      const partKey = `Part${index + 1}/Type`;
      const partName = lesson[partKey] || `Part ${index + 1}`;
      setCurrentPartName(partName);
    } else {
      setCurrentPartName('ENDPOINT');
    }
  };

  const handleNext = () => {
    let newIndex = currentPartIndex + 1;
    const isPartAvailable = (index) => PART_KEYS.some(key => key.startsWith(`Part${index + 1}/`) && lesson[key]);

    while (newIndex < 5 && !isPartAvailable(newIndex)) {
      newIndex++;
    }

    if (newIndex <= 5) {
      setCurrentPartIndex(newIndex);
    }
  };

  const handlePrevious = () => {
    let newIndex = currentPartIndex - 1;
    const isPartAvailable = (index) => PART_KEYS.some(key => key.startsWith(`Part${index + 1}/`) && lesson[key]);

    while (newIndex >= 0 && !isPartAvailable(newIndex)) {
      newIndex--;
    }

    if (newIndex >= 0) {
      setCurrentPartIndex(newIndex);
    }
  };

  const handleFinishLesson = async () => {
    try {
      const endTime = Date.now();
      const timeSpent = Math.round((endTime - startTime) / 1000);

      const lessonStats = {
        timeSpent,
        correctAnswers,
        totalQuestions,
      };

      await finishLesson(lessonID, lessonStats); // Use finishLesson from context

      alert('Lesson completed successfully!');
      navigate('/lesson');
    } catch (error) {
      console.error('Error finishing lesson:', error);
      alert('Failed to finish the lesson. Please try again.');
    }
  };

  const handleTextInputChange = (e, key) => {
    const userInput = e.target.value.toLowerCase().trim();
    const correctAnswer = lesson[key].toLowerCase();

    const isCorrect = userInput === correctAnswer;

    setUserInteractions(prev => ({
      ...prev,
      [key]: {
        value: userInput,
        correct: isCorrect
      }
    }));

    if (isCorrect) {
      setCorrectAnswers(prev => prev + 1);
      e.target.classList.add('correct');
      e.target.classList.remove('incorrect');
    } else {
      e.target.classList.add('incorrect');
      e.target.classList.remove('correct');
    }
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>{error}</div>;
  }

  return (
    <div>
      <LessonHeader currentPartName={currentPartName} lesson={lesson} />
      <LessonNavigation 
        currentPartIndex={currentPartIndex} 
        handleNext={handleNext} 
        handlePrevious={handlePrevious} 
      />
      <RenderLessonParts 
        currentPartIndex={currentPartIndex} 
        lesson={lesson} 
        userInteractions={userInteractions} 
        handleTextInputChange={handleTextInputChange} 
      />
      {currentPartIndex === 5 && <LessonSummary 
        startTime={startTime} 
        correctAnswers={correctAnswers} 
        totalQuestions={totalQuestions} 
        handleFinishLesson={handleFinishLesson} 
      />}
    </div>
  );
};

const LessonHeader = ({ currentPartName, lesson }) => (
  <div className='lp-top yel-red'>
    <div className='lp-top-crsmod'>
      <div className='uppercase'>{lesson.ModuleID}</div> 
      <div>{lesson.lessonName}</div>
    </div>
    <div className='wide'>
      <h1>{currentPartName}</h1>
    </div>
  </div>
);

const LessonNavigation = ({ currentPartIndex, handleNext, handlePrevious }) => (
  <div className='lesson-page'>
    <div className={`navigationL ${currentPartIndex === 0 ? 'disabled' : ''}`} onClick={handlePrevious}>
      <img src={leftIcon} alt="left" className='navIcon-pos'/>
    </div>
    <div className={`navigationR ${currentPartIndex === 5 ? 'disabled' : ''}`} onClick={handleNext}>
      <img src={rightIcon} alt="right" className='navIcon-pos' />
    </div>
  </div>
);


const RenderLessonParts = ({ currentPartIndex, lesson, userInteractions, handleTextInputChange }) => {
  const currentPartKeys = PART_KEYS.filter(
    key => key.startsWith(`Part${currentPartIndex + 1}/`) && !key.includes('/Type')
  );
  
  const randomizeName = (baseName) => `${baseName}_${Math.random().toString(36).substring(7)}`;

  return (
    <div className="mane">
      {currentPartKeys.map((key, index) => {
        const content = DOMPurify.sanitize(lesson[key] || '');
        if (!content) return null;

        const interaction = userInteractions[key] || {};

        if (key.includes('input')) {
          return (
            <input
              key={index}
              type="text"
              data-correct={lesson[key].toLowerCase()}
              name={randomizeName(key)}
              value={interaction.value || ''}
              className={interaction.correct ? 'correct' : interaction.value ? 'incorrect' : ''}
              onChange={(e) => handleTextInputChange(e, key)}
              autoComplete="new-password"
              autoCorrect="off"
              autoCapitalize="none"
              spellCheck="false"
            />
          );
        } else if (key.includes('.Warm-up')) {
          return <div className='warming' key={index} dangerouslySetInnerHTML={{ __html: content }} />;
        } else if (key.includes('.task')) {
          return <h2 key={index} dangerouslySetInnerHTML={{ __html: content }} />;
        } else {
          return <div key={index} className='part' dangerouslySetInnerHTML={{ __html: content }} />;
        }
      })}
    </div>
  );
};

const LessonSummary = ({ startTime, correctAnswers, totalQuestions, handleFinishLesson }) => (
  <div className="endpoint">
    <h2>Lesson Summary</h2>
    <p>Time Spent: {Math.round((Date.now() - startTime) / 1000)} seconds</p>
    <p>Correct Answers: {correctAnswers} / {totalQuestions}</p>
    <button onClick={handleFinishLesson}>Finish Lesson</button>
  </div>
);

export default LessonPage;
