// src/components/AdminPage.js
import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext'; 
import fetchAndUploadData from './admin/fetchAndUpload'; 
import UserManagement from './admin/userManagement'; // Import the UserManagement component
import '../styles/AdminPage.css'

const AdminPage = () => {
  const { user } = useAuth(); 
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [isAdmin, setIsAdmin] = useState(false);

  useEffect(() => {
    if (user) {
      setIsAdmin(user.role === 'admin');
    }
  }, [user]);

  const handleFetchAndUploadData = () => {
    fetchAndUploadData(isAdmin, setLoading, setError);
  };

  return (
    <div>
      <h1>Admin Page</h1>
      {isAdmin ? (
        <>
          <button onClick={handleFetchAndUploadData} disabled={loading}>
            {loading ? 'Uploading...' : 'Fetch and Upload Data'}
          </button>
          <UserManagement /> {/* Include UserManagement here */}
        </>
      ) : (
        <p>You do not have permission to access this page.</p>
      )}
      {error && <p style={{ color: 'red' }}>{error}</p>}
    </div>
  );
};

export default AdminPage;
